import {
  worldArcane,
  worldBera,
  worldCroa,
  worldElysium,
  worldEnosis,
  worldLuna,
  worldNova,
  worldReboot,
  worldRed,
  worldScania,
  worldUnion,
  worldZenith,
  worldAurora
} from "../assets/images";

export function returnWorldIcon(characterWorldName: string) {
  switch (characterWorldName) {
    case "":
      return worldRed;
    case "":
      return worldEnosis;
    case "":
      return worldUnion;
    case "":
      return worldScania;
    case "":
      return worldLuna;
    case "":
      return worldZenith;
    case "":
      return worldCroa;
    case "":
      return worldBera;
    case "":
      return worldElysium;
    case "":
      return worldArcane;
    case "":
      return worldNova;
    case "":
      return worldAurora;
    case "":
      return worldReboot;
    case "2":
      return worldReboot;
  }
}
